import React, { useEffect, useState } from "react";import Seo from "../components/seo";
import Layout from "../components/layout/layout";
import * as layoutStyle from "../components/layout/layout.module.css";
import util from "../js/util";
import Loader from "../components/loader/loader";

const IndexPage = () => {

  const [initialized, setInitialized] = useState(false)
  
  useEffect(() => {
    if (!initialized) {
    fetch("/api/current_login").then(response => {
      if (response.status !== 200) {
        if (response.status === 403) {
          setInitialized(true)
        }
      } else {
        util.navigateToPage("/dashboard")
      }
    })
  }})

  let content = <Loader />

  if (initialized) {
    content = <Layout>
      <div className={ layoutStyle.authError }>
        <p>Invalid user name or password.</p>
      </div>
    </Layout> 
  }

  return (
    <>
      { content }
    </>
  )
}

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Home" />

export default IndexPage
