import { navigate } from "gatsby";
import React, { useState } from "react";
import * as signinStyle from "./signinform.module.css";
import util from "../../js/util";

const SignInForm = ({children}) => {
    const [credetials, setCredentials] = useState({
        userName: "",
        userPassword: ""
    })
    const [isSigninError, setHasSigninError] = useState(false) 
    const handleInputChange = (event) => {
        const target = event.target
        const value = target.value
        const name = target.name

        const newCredentials = Object.assign({}, credetials)
        newCredentials[name] = value

        setCredentials(newCredentials)
    }
    const handleSubmit = (event) => {
        event.preventDefault()

        fetch("/api/do_login", util.addCsrfHeader({
            body: JSON.stringify(credetials),
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            }
        })).then(response => {
            if(response.status == 200) {
                util.navigateToPage("/dashboard")
            } else if (response.status == 401) {
                setHasSigninError(true)
            } else {
                console.log("Login response: ", response)
            }
        })
        //setHasSigninError(true)
    }

    return (
        <div id="signin" className={ signinStyle.signin }>
            <h3>Sign in</h3>
            { isSigninError  && children }
            <form onSubmit={handleSubmit}>
                <label>
                User name:
                <input
                    name="userName"
                    type="text"
                    placeholder="user"
                    onChange={handleInputChange} />
                </label>
                <br />
                <label>
                Password:
                <input
                    name="userPassword"
                    type="password"
                    placeholder="password"
                    onChange={handleInputChange} />
                </label>
                <input type="submit" value="Submit"/>
            </form>
            <div className={ signinStyle.suiteBlock }></div>
        </div>
    )
}

export default SignInForm